@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.App{
    background-image: url(data:image/jpeg;base64,);
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    font-family: 'Open Sans', sans-serif;
    color: rgba(154,156,161,255);
}
.App__container{
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
}
.body{
    display: flex;
    flex-direction: column;
    top: -20px;
    width: 70%;
}
.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 18px;
  align-items: center;
  padding: 5px 20px;
  background-color: #3a53d4;
}
.nav__search {
  display: flex;
  align-items: center;
}
.nav__search__input {
  width: 100%;
  border: none;
  background-color: #3a53d4;
  color: #fff;
  margin-left: 4px;
  font-size: 1em;
}

.nav__search__input:focus {
  outline: none;
}
.nav__icons{
    display: flex;
    align-items: center;
}
.nav__avatar{
    margin-left: 4px;
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%
}
.sidebar {
  display: flex;
  flex-direction: column;
  margin-top: 18px;
  width: 20%;
  align-items: center;
  background-color: rgba(26, 29, 34, 255);
}
.sidebar__top {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: #2647c8;
}
.sidebar__left {
  color: #fff;
  display: flex;
  align-items: center;
}
.sidebar__logo {
  width: 30px;
}
.sidebar__bottom {
  width: 100%;
}

.sidebar__group {
  width: 100%;
  margin-top: 20px;
  display: flex;

  flex-direction: column;
}
.sidebar__option {
  display: flex;
  align-items: center;
  padding: 10px 20px 10px 0;
}
.sidebar__option > * {
  margin-left: 10%;
}
.sidebar__option:hover {
  background-color: #0c0c0c;
  color: rgba(25, 64, 181, 255);
  border-left: 1px solid #2647c8;
}
.sidebar__genre {
  display: flex;
  flex-direction: column;
  padding: 10px 20px 10px 0;
  margin-left: 10%;
}
.sidebar__genre-bold-text {
  font-weight: bold;
  color: #fff;
  font-size: 1em;
  margin-bottom: 10px;
}

.sidebar__downloads {
  display: flex;
  flex-direction: column;
  padding: 10px 20px 10px 0;
  margin-left: 10%;
}
.sidebar__downloads-circle {
  padding: 5px 10px 5px 10px;
  background-color: #0c0c0c;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  left: 45vh;
  border-right: #2647c8 1px solid;
  margin-top: 20px;
}
.sidebar__downloads-cancel {
  color: red;
}
.sidebar__download-img {
  border-radius: 5px;
}

.sidebar__downloads-text {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.sidebar__downloads-details {
  width: 80%;
  margin-left: 10%;
  margin-top: -50px;
}
.sidebar__downloads-progress {
  display: flex;
  width: 100%;
  color: black;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #000;
}
a {
  color: rgba(154, 156, 161, 255);
  text-decoration: none;
}

.movies__container {
  display: flex;
  flex-direction: column;
  margin-left: 2.4rem;
}
.movies {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.movie {
  display: flex;
  flex-direction: column;
  margin-top: 1.4rem;
  width: 15vw;
}
.movie:hover{
  cursor: pointer;
}
.movies__title {
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 1.4rem;
}
.movie__pic {
  height: 40vh;
  object-fit: cover;
  width: 100%;
  border-radius: 15px;
}
.movie__title {
  font-size: 1rem;
  color: white !important;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.movie__adds {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.movie__rating {
  height: 20px;
}

